import { Link } from 'react-router-dom';
import { motion } from 'framer-motion';

function Home() {
  const heroVariants = {
    hidden: { opacity: 0, y: 50 },
    visible: { opacity: 1, y: 0, transition: { duration: 1 } },
  };

  return (
    <div className="min-h-screen flex items-center justify-center bg-gradient-to-b from-[var(--primary)] to-[var(--accent)]">
      <motion.div
        className="text-center glassmorphism p-8 max-w-2xl mx-4"
        variants={heroVariants}
        initial="hidden"
        animate="visible"
      >
        <h1 className="text-4xl md:text-5xl font-bold text-primary mb-4">
          <i className="fas fa-globe-americas mr-2"></i>Plan Your Dream Adventure
        </h1>
        <p className="text-lg text-neutral-gray mb-6">
          Organize your trips with ease, explore destinations, and make unforgettable memories with Wonder Wise.
        </p>
        <Link
          to="/signup"
          className="gradient-button px-6 py-3 text-lg"
        >
          <i className="fas fa-user-plus mr-2"></i>Get Started
        </Link>
      </motion.div>
    </div>
  );
}

export default Home;